120px;\n  padding: 20px;\n  background: rgba(0,0,0,0.0);\n  color: rgba(255,255,255,1);\n  font-family: Olondona;\n  text-align: center;\n  line-height: 1.3em;\n  font-weight: 100;\n  font-size: 14px;\n  border-radius: 4px;\n  opacity: 0;\n  transition: opacity 0.3s;\n  pointer-events: none;\n\n}\n\n.modal {\n  display: none; /* Hidden by default */\n  position: fixed; /* Stay in place */\n  z-index: 1; /* Sit on top */\n  left: 0;\n  top: 0;\n  width: 100%; /* Full width */\n  height: 100%; /* Full height */\n  overflow: auto; /* Enable scroll if needed */\n  background-color: rgb(0,0,0); /* Fallback color */\n  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */\n}\n\n\n/* Modal Content/Box */\n.modal-content {\n  background-color: #fefefe;\n  margin: 15% auto; /* 15% from the top and centered */\n  padding: 20px;\n  border: 1px solid #888;\n  width: 80%; /* Could be more or less, depending on screen size */\n}\n\n/* The Close Button */\n.close {\n  color: #aaa;\n  float: right;\n  font-size: 28px;\n  font-weight: bold;\n}\n\n.close:hover,\n.close:focus {\n  color: black;\n  text-decoration: none;\n  cursor: pointer;\n}\n\n\n.title{\n  font-size: 3em;\n  font-family: Passio;\n  // text-align: center;\n  position: absolute;\n  color: rgba(255,255,255,1);\n  // pointer-events: none;\n  top: 5%;\n  left: 5%;\n  // justify-content: center;\n  width: 100%;\n  height: 20px;\n  z-index: 1;\n  cursor: pointer;\n}\n\n\n.fade-out {\n\n  animation: fadeOut ease 4s  forwards;;\n  -webkit-animation: fadeOut ease 4s  forwards;;\n  -moz-animation: fadeOut ease 4s  forwards;;\n  -o-animation: fadeOut ease 4s  forwards;;\n  -ms-animation: fadeOut ease 4s  forwards;;\n}\n@keyframes fadeOut {\n  0% {\n    opacity:1;\n  }\n  100% {\n    opacity:0;\n  }\n}\n\n@-moz-keyframes fadeOut {\n  0% {\n    opacity:1;\n  }\n  100% {\n    opacity:0;\n  }\n}\n\n@-webkit-keyframes fadeOut {\n  0% {\n    opacity:1;\n  }\n  100% {\n    opacity:0;\n  }\n}\n\n@-o-keyframes fadeOut {\n  0% {\n   